'use strict';
// 监听返回
import {fnError, WinJSDec} from './APP/config';

var name = 'CALLBACK_BACK';
var CALLBACK_BACK = (fn = _ => {
}) => {
  if (typeof fn === 'function') {
    WinJSDec(_ => {
      window.WinJSBridge.call('webview', 'backListen', {enable: true});
      document.addEventListener('clickback', fn);
    });
  } else {
    throw new TypeError(`[${name}] ${fnError}`);
  }
};
export default CALLBACK_BACK;
